.about-banner-text {
    color: #f0f0f0 !important;
    font-size: 20px;
    letter-spacing: 2px;
    line-height: 2em;
}

.about-banner-text span {
    font-size: 30px;
    color:  #a420be;
}

.about-banner-text p{
    margin: 0px;
    color: #f0f0f0;
    font-size: 20px;
    padding: 1em;
}

li {
    font-family: "Libre Baskerville", serif; 
    list-style-type: circle;
    margin: 3px;
    padding: auto;
    justify-content: left;
}

.about-name {
    font-family: 'Dancing Script', cursive;
    font-size: 40px !important;
}

@media only screen and (max-width: 768px) {
    .about-banner-text {
        font-size: 15px;

    }

    .about-banner-text span {
        font-size: 22.5px;
    }

    .about-name {
        font-size: 30px !important;
    }
}

@media only screen and (max-width: 420px) {
    .about-banner-text {
        font-size: 12px;
    }

    .about-banner-text span {
        font-size: 18px;
    }
    
    .about-name {
        font-size: 24px !important;
    }
}
