.about-card{
    font-family: "Libre Baskerville", serif; 
    display: inline-block;
    background:  #333333;
    border-bottom: 2px solid #231f20;
    margin: 10% 1% 0% 1%;
    position: relative;
    cursor: pointer;
    overflow: hidden; /* Makes the effect of the card */
    -webkit-transition: -webkit-transform 0.5s;
    transition: -webkit-transform 0.5s;
    transition: transform 0.5s;
    transition: transform 0.5s, -webkit-transform 0.5s;
    border-radius: 5%;
    min-width: 175px;
    padding-top: 0.75%;
    text-align: center;
}

.about-card-medium {
    width: 18vw;
    height: 8vw;
    min-width: 175px;
    min-height: 82.5px;
}

.about-card-large {
    width: 50%;
    height: 15em;
}

.about-card a {  
    font-size: 100%;
    text-align: justify;
    color: #f0f0f0;
    padding: 8px;
    text-decoration: none;
    margin: 0px;
    padding: 0px;
}

.about-card span {
    width: 100%;
    font-family: 'Dancing Script';
    font-size: 130%;
    color: #f0f0f0;
    padding: 8px;
    text-decoration: none;
    margin: 0px;
}

.about-card i {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    font-size: 2.5vw;
    line-height: 300%;
    text-align: center;
    background: -webkit-linear-gradient(#333333, #231f20);
    background: linear-gradient(#333333, #231f20);
    z-index: 1;
    -webkit-transition: -webkit-transform 0.5s;
    transition: -webkit-transform 0.5s;
    transition: transform 0.5s;
    transition: transform 0.5s, -webkit-transform 0.5s;
}

.about-card:hover {
    border-bottom: 2px solid #a420be;
}

.about-card:hover i{
-webkit-transform: translateY(35%);
        transform: translateY(35%);
}

@media only screen and (max-width: 768px) {
    .about-card i {
        font-size: 4.5vw;
        line-height: 250%;
    }
    
    .about-card {
        margin: 6% 6%;
    }
}

@media only screen and (max-width: 420px) {
    .about-card i {
        line-height: 300%;
        font-size: 6vw;
    }
}
